import React, { useState } from "react";
import style from "./FilterBar.module.css";
import Select from "react-select";

const FilterBar = (props) => {
  const optionsColumn = [
    { value: "Date", label: "" },
    { value: "Name", label: "" },
    { value: "Amount", label: "" },
    { value: "Distance", label: "" },
  ];
  const optionsCondition = [
    { value: "More", label: "" },
    { value: "Less", label: "" },
    { value: "Includes", label: "" },
    { value: "Equal", label: "" },
  ];

  const [column, setColumn] = useState("");
  const [condition, setCondition] = useState("");
  const [value, setValue] = useState("");

  let onSettingFilteringValue = (e) => {
    let filterText = e.target.value;
    setValue(filterText);
    props.setFilteringValue(filterText);
  };

  const onFilteringColumn = (type) => {
    props.setColumnFilter(type.value);
    setColumn(type);
  };
  const onFilteringCondition = (type) => {
    props.setConditionFilter(type.value);
    setCondition(type);
  };
  const onFiltering = () => {
    props.filterGoods();
  };
  const onResetFilters = () => {
    props.resetFilters();
    setColumn("");
    setCondition("");
    setValue("");
  };
  return (
    <div className={style.FilterBar}>
      <div className={style.FiltersSection}>
        <Select
          value={column}
          className={style.Selectors}
          classNamePrefix="selectors"
          placeholder=" "
          options={optionsColumn}
          isSearchable={false}
          onChange={onFilteringColumn}
        />
        <Select
          value={condition}
          className={style.Selectors}
          placeholder=" "
          classNamePrefix="selectors"
          options={optionsCondition}
          isSearchable={false}
          onChange={onFilteringCondition}
        />
        <input
          placeholder={column.value === "Date" && ".."}
          value={value}
          onChange={onSettingFilteringValue}
          style={{ width: 80 + "px", height: 30 + "px" }}
        />
        <button
          onClick={onFiltering}
          className={style.SetFiltersButton}
          style={{ width: 140 + "px" }}
        >
          {" "}
          {" "}
        </button>
      </div>
      <button
        onClick={onResetFilters}
        className={style.SetFiltersButton}
        style={{ width: 200 + "px" }}
      >
         
      </button>
    </div>
  );
};

export default FilterBar;
